<template>
  <div class="hello">
    <div v-if="state === 'upload'">
      <h1>SEND ME SOMETHING</h1>
      <input type="file" ref="file">
      <br><br>
      <button @click="upload()">SEND</button>
    </div>
    <div v-if="state === 'uploading'">
      <h1>SENDING</h1>
      Your file ({{selectedFileName}}) is sending now.
      <br>
      Please do not close this tab...
    </div>
    <div v-if="state === 'error'">
      <h1> ¯\_(ツ)_/¯</h1>
      Something went wrong with your upload...
      <br>
      Please refresh the page and try again.
    </div>
    <div v-if="state === 'success'">
      <h1> SUCCESS </h1>
      Your file ({{selectedFileName}}) has been sent.
      <br>
      <button @click="reset()">SEND ANOTHER FILE</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Upload',
  props: {
    user: Object
  },
  data:()=>{
    return {
      selectedFileName: 'none',
      state: 'upload',// [upload, uploading, finished]
    }
  },
  methods:{
    upload: async function(){
      this.selectedFileName = this.$refs.file.files[0].name;
      let formData = new FormData();
      formData.append('upload', this.$refs.file.files[0]);
      //formData.append('fileName', this.fileName);
      formData.append('token', this.user.token);
      this.state = 'uploading';
      try{
        const response = await axios.post('/upload/', formData);
        if(response.status === 200){
          //this.$refs.file.value = '';
          this.state = 'success';
        }
        else{
          this.state = 'error';
        }
      }catch(err){
        console.log(err);
        this.state = 'error';
      }
    },
    reset: function(){
      this.state = 'upload';
    }
  }
}
</script>

