	--bg3: rgb(calc(var(--pr)*0.7), calc(var(--pg)*0.7), calc(var(--pb)*0.7));
	--bg4: rgb(calc(var(--pr)*0.6), calc(var(--pg)*0.6), calc(var(--pb)*0.6));
	--bg5: rgb(calc(var(--pr)*0.5), calc(var(--pg)*0.5), calc(var(--pb)*0.5));
	--bg6: rgb(calc(var(--pr)*0.4), calc(var(--pg)*0.4), calc(var(--pb)*0.4));
